/* credits effect css */
.wrapper {
  position: relative;
  transform: translateY(100%);
  animation: 20s credits linear;
  animation-fill-mode: forwards;
}

.container{
  overflow: hidden;
}

@keyframes credits {
  0% {
    transform: translateY(600%);
  }
  100% {
    transform: translateY(-200%);
  }
}

